.cart {
  margin: 2rem auto;
  width: 82%;
  display: flex;
  justify-content: space-between;
}

.cart__items {
  flex: 0.7;
  margin-right: 1rem;
}

.cart__summary {
  height: 31vh;
  flex: 0.3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 10px;
}

.summary__title {
  font-size: 1.2rem;
  font-weight: bold;
}

.summary__price {
  display: flex;
  align-items: center;
}

.summary__price span:nth-child(1) {
  font-size: 0.9rem;
  flex: 0.6;
}

.summary__price span:nth-child(2) {
  flex: 0.4;
  font-size: 1.2rem;
  font-weight: bold;
}

.summary__checkoutBtn {
  height: 2.5rem;
  width: 16rem;
  margin-left: 1.8rem;
  border-radius: 10px;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  background-color: rgb(41, 41, 126);
  color: white;
}

.summary__checkoutBtn:focus {
  outline: none;

  border: 6px solid rgb(41, 41, 126);
}